import { PaymentRequestDTO } from "@/common/dto/payment-request.dto";
import { Controller, Post } from "@nestjs/common";
import { PaymentService } from "./payment.servicee";

@Controller('payment')
export class PaymentController {

 constructor(public paymentService: PaymentService) {}

  @Post('create')
  createPayment(paymentRequestDTO: PaymentRequestDTO) {
    return this.paymentService.createPayment(paymentRequestDTO);
  }
}